import AppLayout from '@/layout/AppLayout.vue';
import { createRouter, createWebHistory } from 'vue-router';
import workRoutes from './routes/work.js';
import matRoutes from './routes/mat.js';
import qualityRoutes from './routes/quality.js';
import sellerRoutes from './routes/seller.js';
import facRoutes from './routes/fac.js';
import adminRouter from './routes/admin.js';
import { useUserStore } from '@/stores/user';
import axios from 'axios';
const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/',
            component: AppLayout,
            
            children: [
                {
                    path: '/',
                    name: 'main',
                    component: () => import('@/views/pages/MainView.vue')
                },
                ...workRoutes,
                ...matRoutes,
                ...qualityRoutes,
                ...facRoutes,
                ...sellerRoutes,
                ...adminRouter,
                
            ]
        },
        {       path: '/auth/login',
                name: 'login',
                component: () => import('@/views/pages/auth/Login.vue')
        },
        {
            path:'/error',
            name:'error',
            component : () =>import('@/views/pages/Error.vue')
        },
        // {
        //     path: '/landing',
        //     name: 'landing',
        //     component: () => import('@/views/pages/Landing.vue')
        // },
        // {
        //     path: '/pages/notfound',
        //     name: 'notfound',
        //     component: () => import('@/views/pages/NotFound.vue')
        // },

        // {
        //     path: '/auth/login',
        //     name: 'login',
        //     component: () => import('@/views/pages/auth/Login.vue')
        // },
        // {
        //     path: '/auth/access',
        //     name: 'accessDenied',
        //     component: () => import('@/views/pages/auth/Access.vue')
        // },
        // {
        //     path: '/auth/error',
        //     name: 'error',
        //     component: () => import('@/views/pages/auth/Error.vue')
        // }
    ]
    
    
});
//   beforeEach    
//to    
//from     
//next()    
router.beforeEach(async (to, from, next) => {
    const store = useUserStore();
    //    URL   
    const publicPages = ['/auth/login','/error'];
    //   URL     
    const authRequired = !publicPages.includes(to.path);
    //    
    if (!store.id) {

      try {
        const res = await axios.get('/api/session');
        console.log(res);
        if (res.data.isLogin) {
          store.setUser({
            id: res.data.id,
            empName: res.data.empName,
            dep: res.data.dep
          });
        }
      } catch (e) {
        console.log(e)
      }
    }
  
    //        
    if (authRequired && !store.isLogin) {
     return next('/auth/login');
    }
    //  
    //startWith /admin   TRUE  FALSE
    if (to.path.startsWith('/admin') && store.dep !== '') {
      if(store.dep !== ''){
        return next('/error');
       }
      }
      if (to.path.startsWith('/mat') && store.dep !== '') {
        if(store.dep !== ''){
        return next('/error');
       }
      }
      if(to.path.startsWith('/work') && store.dep !== ''){
        if(store.dep !== ''){
          return next('/error');
         }
      }
      if(to.path.startsWith('/qual') && store.dep !== ''){
        if(store.dep !== ''){
          return next('/error');
         }
      }
      if(to.path.startsWith('/fac') && store.dep !== ''){
        if(store.dep !== ''){
          return next('/error');
         }
      }
      if(to.path.startsWith('/seller') && store.dep !== ''){
        if(store.dep !== ''){
          return next('/error');
         }
      }
    
      //     
      next();
  });

export default router;
